
.wrapper{
    width: 100vw;
  }
  
  .sidebar{
    height: 100vh;
    width: 5vw;
  }
  .sidebarlink{
    height: 50px;
    display: flex;
    align-items: center;
  }
  .sidebar-option{
    font-size: 2.3vh;
    color: #bcdcff;
  }
  .sidebar-option:active{
    color: white;
  }
  .sidebar-option:hover{
    color: white;
  }
  .logo{
    width: 30px;
  }
  .sidebar-header{
    height: 70px;
  }
  .sidebar-opt:hover{
    width: 100%;
    display: flex;
    justify-content: center;
    color: white;
  }
  .stepper-bar{
    color: white;
    padding: 12px;
    width: 18vw;
    display: flex;
    justify-self: center;
    align-self: center;
  }